import resolve from 'resolve';
import { Plugin } from '../plugin';
import { ServerContext } from '../server';
import path from 'path';
import { pathExists } from 'fs-extra';
import { DEFAULT_EXTENSIONS } from '../constants';
import { cleanUrl, normalizePath } from '../utils';

export function resolvePlugin(): Plugin {
  let serverContext: ServerContext;
  return {
    name: 'm-vite:resolve',
    configureServer(s) {
      // ‰øùÂ≠òÊúçÂä°Á´Ø‰∏ä‰∏ãÊñá
      serverContext = s;
    },
    async resolveId(id: string, importer?: string) {
      //ÁªùÂØπË∑ØÂæÑ
      if (path.isAbsolute(id)) {
        if (await pathExists(id)) {
          return { id };
        }
        //Â§ÑÁêÜÁªùÂØπË∑ØÂæÑ‰∏≠ ‚Äò/src/main.tsx ÁöÑÊÉÖÂÜµ
        id = path.join(serverContext.root, id);
        if (await pathExists(id)) {
          return { id };
        }
      }
      //Áõ∏ÂØπË∑ØÂæÑÁöÑÊÉÖÂÜµ
      else if (id.startsWith('.')) {
        if (!importer) {
          throw new Error('"importer" should not be undefined');
        }
        // console.log('importer------->',importer);

        const hasExtension = path.extname(id).length > 1;
        let resolveId: string;
        //ÂåÖÂê´Êñá‰ª∂ÂêçÂêéÁºÄ
        //Â¶Ç ./App.tsx
        if (hasExtension) {
          resolveId = normalizePath(
            resolve.sync(id, { basedir: path.dirname(importer) })
          );
          // console.log('resolve,ts-------->',resolveId);

          if (await pathExists(resolveId)) {
            // return {id:resolveId}
            return { id };
          }
        }
        //‰∏çÂåÖÂê´Êñá‰ª∂ÂêçÂêéÁºÄ
        else {
          for (const extname of DEFAULT_EXTENSIONS) {
            try {
              const withExtension = `${id}${extname}`;
              resolveId = normalizePath(
                resolve.sync(withExtension, {
                  basedir: path.dirname(importer)
                })
              );
              // console.log(resolveId);

              if (await pathExists(resolveId)) {
                return { id: withExtension };
              }
            } catch (e) {
              continue;
            }
          }
        }
      }
      return null;
    }
  };
}
