import { NextHandleFunction } from 'connect';
import { ServerContext } from '../index';
import path from 'path';
import { pathExists, readFile } from 'fs-extra';

export function indexHtmlMiddleware(
  serverContext: ServerContext
): NextHandleFunction {
  return async (req, res, next) => {
    if (req.url === '/') {
      const { root } = serverContext;
      //ÈªòËÆ§‰ΩøÁî®Ê†πÁõÆÂΩï‰∏ãÁöÑindex.html
      const indexHtmlPath = path.join(root, 'index.html');
      if (await pathExists(indexHtmlPath)) {
        const rawHtml = await readFile(indexHtmlPath, 'utf-8');
        let html = rawHtml;
        // ‰∏ÄÊ¨°Â∞èÂ∞ùËØïÔºåÂ∞Üvite AppÊõøÊç¢‰∏∫xpppp
        html = html.replace('Vite App', 'xpppp');
        // console.log('-------html',html);

        //ÈÄöËøáÊâßË°åÊèí‰ª∂ÁöÑtransformindexhtmlÊñπÊ≥ïÊù•ËøõË°åËá™ÂÆö‰πâ‰øÆÊîπ
        for (const plugin of serverContext.plugins) {
          if (plugin.transformIndexHtml) {
            html = await plugin.transformIndexHtml(html);
          }
        }
        res.statusCode = 200;
        res.setHeader('Content-type', 'text/html');
        return res.end(html);
      }
      return next();
    }
  };
}
