import { Plugin, Loader } from 'esbuild';
import { BARE_IMPORT_RE } from '../constants';
// Áî®Êù•ÂàÜÊûêesÊ®°ÂùóÁöÑ import/export ËØ≠Âè•ÁöÑÂ∫ì
import { init, parse } from 'es-module-lexer';
import path from 'path';
//‰∏Ä‰∏™ÂÆûÁé∞‰∫ÜnodeË∑ØÂæÑËß£ÊûêÁÆóÊ≥ïÁöÑÂ∫ì
import resolve from 'resolve';
// ‰∏Ä‰∏™Êõ¥Âä†Â•ΩÁî®ÁöÑÊñá‰ª∂Êìç‰ΩúÂ∫ì
import fs from 'fs-extra';
//Áî®Êù•ÂºÄÂèëÊâìÂç∞ debug Êó•ÂøóÁöÑÂ∫ì
import createDebug from 'debug';
import { normalizePath } from '../utils';
import { ExportSpecifier } from 'es-module-lexer';

const debug = createDebug('dev');

export function preBundlePlugin(deps: Set<string>): Plugin {
  return {
    name: 'esbuild:pre-bundle',
    setup(build) {
      build.onResolve(
        {
          filter: BARE_IMPORT_RE
        },
        (resolveInfo) => {
          const { path: id, importer } = resolveInfo;
          const isEntry = !importer;
          //ÂëΩ‰∏≠ÈúÄË¶ÅÈ¢ÑÊûÑÂª∫ÁöÑ‰æùËµñ
          if (deps.has(id)) {
            //Â¶ÇÊûú‰∏∫ÂÖ•Âè£ÔºåÂàôÊ†áËÆ∞depÁöÑnamespace
            return isEntry
              ? {
                  path: id,
                  namespace: 'dep'
                }
              : {
                  //Âõ†‰∏∫Ëµ∞Âà∞ onResolve ‰∫ÜÔºåÊâÄ‰ª•ËøôÈáåÁöÑpath Â∞±ÊòØÁªùÂØπË∑ØÂæÑ‰∫Ü
                  path: resolve.sync(id, { basedir: process.cwd() })
                };
          }
        }
      );
      //ÊãøÂà∞Ê†áËÆ∞ÂêéÁöÑÊ®°ÂùóÔºåÊûÑÈÄ†‰ª£ÁêÜÊ®°ÂùóÔºå‰∫§ÁªôesbuildÊâìÂåÖ
      build.onLoad(
        {
          filter: /.*/,
          namespace: 'dep'
        },
        async (loadInfo) => {
          //es-module-lexerÁöÑinitÂàùÂßãÂåñ
          await init;
          const id = loadInfo.path;
          const root = process.cwd();
          //Ëé∑ÂæóË∑ØÂæÑ
          const entryPath = normalizePath(resolve.sync(id, { basedir: root }));
          // ËØªÂèñÊñá‰ª∂ÂÜÖÂÆπ
          const code = await fs.readFile(entryPath, 'utf-8');
          //esÊ®°ÂùóÁöÑËß£ÊûêÔºåcjsËß£Êûê‰∏çÂà∞
          const [imports, exports] = await parse(code);
          const proxyModule = [];

          let relativePath = normalizePath(path.relative(root, entryPath));
          if (
            !relativePath.startsWith('./') &&
            !relativePath.startsWith('../') &&
            relativePath !== '.'
          ) {
            relativePath = `./${relativePath}`;
          }

          //cjs
          // ÊâÄ‰ª•ËøôÈáå‰∏∫Á©∫‰ª£Ë°®cjsÊ®°Âùó
          if (!imports.length && !exports.length) {
            //ÊûÑÈÄ†‰ª£ÁêÜÊ®°Âùó
            const res = require(entryPath);
            const specifiers = Object.keys(res);
            // console.log('res',res);

            // console.log("specifiers------->", specifiers);

            proxyModule.push(
              `export { ${specifiers.join(',')} } from "${relativePath}"`,
              `export default require("${relativePath}")`

              // `export {${specifiers.join(",")} } from "${entryPath}"`,
              // `export default require("${entryPath})`
            );
          } else {
            //esmÊ†ºÂºèÁöÑÂ§ÑÁêÜ
            if (exports.includes('default' as any as ExportSpecifier)) {
              proxyModule.push(
                `import d from "${relativePath}";export default d`
              );
              // proxyModule.push(`import d from "${entryPath}";export default d`);
            }
            proxyModule.push(`export * from "${relativePath}"`);
            // proxyModule.push(`export * from "${entryPath}"`);
          }
          debug('‰ª£ÁêÜÊ®°ÂùóÂÜÖÂÆπÔºö%o', proxyModule.join('\n'));
          const loader = path.extname(entryPath).slice(1);
          return {
            loader: loader as Loader,
            contents: proxyModule.join('\n'),
            resolveDir: root
          };
        }
      );
    }
  };
}
