import os from "os";
import path from "path";
import { JS_TYPES_RE, HASH_RE, QUERY_RE } from "./constants";

//ÂØπwindowÁîµËÑëËøõË°åÈÄÇÈÖç
export function slash(p: string): string {
  return p.replace(/\\/g, "/");
}

//Âà§Êñ≠ÊòØÂê¶ÊòØwindowÁîµËÑë
export const isWindows = os.platform() === "win32";

//ÂØπË∑ØÂæÑËøõË°åwindowÈÄÇÈÖç
export function normalizePath(id: string): string {
  return path.posix.normalize(isWindows ? slash(id) : id);
}

//È°æÂêçÊÄù‰πâÔºåÊ∏ÖÁêÜurlÔºåÂéªÈô§hashÁ≠â‰ø°ÊÅØ
export const cleanUrl = (url: string): string => url.replace(HASH_RE, "").replace(QUERY_RE, "");

//
export const isJsRequest = (id: string): boolean => {
  id = cleanUrl(id);
  if (JS_TYPES_RE.test(id)) {
    return true;
  }
  if (!path.extname(id) && id.endsWith("/")) {
    return true;
  }
  return false;
};

export const isCssRequest = (id: string): boolean => cleanUrl(id).endsWith(".css");

//ÈùôÊÄÅËµÑÊ∫êËØ∑Ê±Ç
export function isImportRequest(url: string): boolean {
  return url.endsWith("?import");
}

//Â∫îËØ•ÊòØËøîÂõûÁõ∏ÂØπË∑ØÂæÑ
export function getShortName(file: string, root: string) {
  return file.startsWith(root + "/") ? path.posix.relative(root, file) : file;
}

//ÂéªÈô§ÂêéÈù¢Ê∑ªÂä†ÁöÑimport
export function removeImportQuery(url: string): string {
  return url.replace(/\?import$/, "");
}
